*{
    margin: 0;
    padding: 0;
}
h1{
    text-align: center;
    line-height: 80px;
    color: rgb(255, 136, 0);
    font-size: 40px;
font-style: italic;
}
.ree{
    background: green;
    color: white;
    font-size: 20px;
    border-radius: 10px;
    box-sizing: content-box;
    margin-right: 10px;
}
input[type="text"]
{
    height: 36px;
    width: 100px;
    text-align: center;
    list-style: -o-linear-gradient(20px);
    margin-left: 40px;
    color: rgb(56, 8, 78);
    background-color: transparent;
}
.see{
    position: relative;
    margin-top: 10%;
    margin-left: 500px;
    margin-right: 500px;
border: 2px solid rgb(204, 91, 16);
background:  aquamarine;

}
span{
    margin-left: 20px;
    line-height: 20px;
    list-style-position: inside;
    font-size: 30px;
}
button{
    padding: 10px;
    
}
ul li{
    list-style-type: none;
}